*{margin: 0; padding: 0;}
li{list-style: none;}

body,
html {
  width: 100%;
  background-color: #f8f8f8;
}

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Poppins:wght@300;400;500;600;700&display=swap');

@mixin kor {
  font-family: "Noto Sans KR";
  font-style: normal;
  font-weight: 400;
}

@mixin eng {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
}


/* ----------------------- header ----------------------- */
.header {
  width: 1420px;
  height: 220px;
  background-color: #f8f8f8;
  //display: flex;

  .logo {
    width: 55px;
    height: auto;
    padding: 18px 0 5px;
    box-sizing: border-box;
    margin-left: 35px;
    cursor: pointer;

    a {
      width: 55px;
      height: auto;

      img {
        width: 100%;
      }
    }
  }
}